export default "\n.borderBox {\n  flex-grow: 1;\n  height: -webkit-min-content;\n  height: -moz-min-content;\n  height: min-content;\n}\n.container {\n  background: #f9f9f9;\n  height: 100vh;\n  width: 100vw;\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n}\n.numberBoxTitle > span {\n  font-size: 1.4rem;\n}\n.title {\n  font-size: 2.5em;\n  font-weight: bold;\n  /* font-family: youshe; */\n  white-space: nowrap;\n  /* padding-top: 10vh; */\n  margin-left: 5vw;\n  color: #4d4d4d;\n}\n.title span {\n  color: #93d500;\n}\n.inline {\n  white-space: nowrap;\n}\n.content {\n  width: 90vw;\n  display: flex;\n  /* height: 50vh; */\n  flex-direction: column;\n  justify-content: center;\n  margin: 2em auto;\n  margin-top: 0;\n}\n.transparent {\n  background: transparent !important;\n}\n.item {\n  font-weight: normal !important;\n  color: #808080 !important;\n}\n.numberBox {\n  display: flex;\n  height: 7rem;\n  flex-wrap: nowrap;\n  flex-shrink: 1;\n  height: -webkit-min-content;\n  height: -moz-min-content;\n  height: min-content;\n}\n.number {\n  width: 100%;\n  /* height: 100vh; */\n  display: flex;\n  justify-content: center;\n  flex-direction: column;\n}\n.numberBoxTitle {\n  white-space: nowrap;\n  color: #666666;\n  font-weight: bolder;\n  overflow: visible;\n  margin: 0 1em;\n  transform: translateY(1em);\n  height: 1em;\n  font-size: 1.2rem;\n}\n.describeBox {\n  width: 100%;\n  overflow: visible;\n  margin-top: 0.5em;\n  font-size: 1rem;\n  line-height: 1.7;\n}\n.colorful {\n  background: linear-gradient(45deg, rgb(244, 246, 250), rgb(216, 224, 236));\n  -webkit-background-clip: text;\n  background-clip: text;\n  color: transparent;\n  font-size: 7em;\n  font-weight: bold;\n  /* font-style: italic; */\n  /* 手机端 没有相关字体包会导致问题 */\n  height: 7rem;\n  line-height: 7rem;\n  display: inline-block;\n}\ncommon-button93d500 {\n  display: block;\n  width: -webkit-min-content;\n  width: -moz-min-content;\n  width: min-content;\n  margin: 0 auto;\n  margin-bottom: 5em;\n}\ncommon-dasharrow {\n  /* position: absolute; */\n  transform-origin: left center;\n  transform: rotate(90deg) translate(-50%, 4px);\n  /* 用0占位不算优雅 */\n}\n.step {\n  font-weight: bold;\n}\n.fill1 {\n  width: 1em;\n  height: 1em;\n}\n"