export default "\n.container {\n  width: 100%;\n  height: 250px;\n  box-sizing: border-box;\n  overflow-x: scroll;\n  overflow-y: visible;\n}\n.scaleBox,\n.dataBox {\n  display: inline-flex;\n  height: 100%;\n}\n.container::-webkit-scrollbar {\n  display: none;\n}\n.scaleBox {\n  width: 2em;\n  height: 200px;\n  flex-direction: column-reverse;\n  align-items: center;\n  justify-content: space-between;\n  position: sticky;\n  z-index: 6;\n  left: 0;\n  background: white;\n  opacity: 1;\n  border-right: 2px solid #ddd;\n}\n.data::after,\n.data::before {\n  width: 1.5em;\n  text-align: center;\n  content: \"\";\n  position: absolute;\n  z-index: 3;\n}\n.data::after {\n  content: attr(data-time);\n  bottom: -2em;\n}\n.data::before {\n  content: attr(data-value);\n  top: -2em;\n}\n.holder {\n  height: 45px;\n  width: 2em;\n  position: absolute;\n  background: white;\n  opacity: 1;\n  top: 200px;\n  border: 2px solid white;\n}\n.data {\n  width: 1.5em;\n  display: inline-block;\n  position: relative;\n  overflow: visible;\n  background-color: #93d500;\n  padding: 0 1em;\n  flex-shrink: 0;\n  border-bottom: 2px solid #93d500;\n  background-clip: content-box;\n}\n.dataBox {\n  width: calc(600px - 2em);\n  height: 200px;\n  /* overflow-x: scroll; */\n  /* overflow-y: hidden; */\n  align-items: flex-end;\n}\n.dataBox::-webkit-scrollbar {\n  display: none;\n}\n"