export default "\ndiv.watch {\n  background-position: center;\n  background-size: contain;\n  background-repeat: no-repeat;\n  position: relative;\n  transform-origin: left top;\n  height: 100px;\n  width: 100px;\n  --shadow: 0 0 1px 0 #ddd, 0 0 4px 0 rgba(242, 242, 242, 0.77);\n}\n.watch,\n.second,\n.hour,\n.minute {\n  display: inline-block;\n  position: absolute;\n  height: 40px;\n}\n.circle {\n  position: absolute;\n  top: 46px;\n  height: 8px;\n  width: 8px;\n  left: 46px;\n  background: #fff;\n  z-index: 2;\n  border-radius: 50%;\n  box-shadow: var(--shadow);\n}\n.second,\n.hour,\n.minute {\n  transform-origin: bottom;\n  width: 4px;\n  top: 10px;\n  background: #fff;\n  margin: 0 48px;\n  border-radius: 3px;\n  box-shadow: var(--shadow);\n  /* transition: transform 0.5s linear; */\n}\n.second {\n  width: 2px;\n  height: 49px;\n  top: 1px;\n  margin: 0 49px;\n  border-radius: 2px;\n}\n.hour {\n  width: 6px;\n  margin: 0 47px;\n  height: 30px;\n  top: 20px;\n  border-radius: 5px;\n}\n"