export default "\n.backTo {\n  text-decoration: none;\n  cursor: pointer;\n  color: white;\n  font-size: max(1.5vw, 1em);\n  font-weight: 200;\n}\n.backTo:hover {\n  font-weight: 400;\n}\n.wrapper {\n  display: flex;\n  align-items: center;\n}\n.header {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  z-index: 2000;\n  width: 100vw;\n  height: 8.177vw;\n  /* background-image: url(); */\n  background-size: 100% 100%;\n  transition: background 150ms, box-shadow 150ms;\n  overflow: hidden;\n}\n.yingtan {\n  position: absolute;\n  top: 40%;\n  left: 5%;\n  transform: translateY(-50%);\n  display: flex;\n  flex-wrap: nowrap;\n  justify-content: space-between;\n  width: 90%;\n  align-items: center;\n}\n.yingtan span {\n  padding-left: 1vw;\n  margin-left: 1vw;\n  border-left: min(0.2vw, 2px) solid white;\n  color: white;\n  font-weight: 200;\n  font-size: max(1.5vw, 1em);\n  line-height: 0.9;\n}\n.yingtanimg {\n  width: 15vw;\n}\n.header-upper,\n.header-lower {\n  display: none;\n}\n.header-upper {\n  height: 80%;\n}\n.routerTest {\n  position: absolute;\n  left: 200px;\n}\n.bt-down {\n  margin: 25px 0;\n  border: solid white;\n  border-radius: 50%;\n  position: fixed;\n  left: 50%;\n  bottom: 0;\n  transform: translateX(-50%);\n  width: 45px;\n  height: 45px;\n  /* background-color: transparent; */\n  background: #93d500;\n  z-index: 10001;\n  /* 确保最高层级 */\n  cursor: pointer;\n}\n.bt-up {\n  transform: translate(-50%) rotate(-180deg);\n  /* 旋转父盒子解决返回顶部箭头不居中问题 */\n}\n.arrow {\n  position: absolute;\n  /* background-image: url(); */\n  background-position-x: center;\n  background-repeat: no-repeat;\n  background-size: contain;\n  width: 20px;\n  height: 20px;\n  content: \"\";\n  display: block;\n  top: 55%;\n  left: 50%;\n  transform: translate(-50%, -50%) rotate(90deg);\n}\n@media only screen and (max-width: 483px) {\n.header {\n    height: 15vw;\n    /* background-image: url(); */\n}\n.yingtanimg {\n    width: min(32vw, 8em);\n}\n.yingtan {\n    margin-top: 5px;\n}\n}\n"