export default "\n.show.container {\n  z-index: 10000;\n  opacity: 1;\n}\n.container {\n  position: absolute;\n  z-index: -1;\n  top: 50vh;\n  left: 50%;\n  width: 70vw;\n  height: 50px;\n  display: flex;\n  transition: opacity 0.2s ease;\n  transform: translate(-50%, -25px);\n  justify-content: center;\n  opacity: 0;\n}\n.success,\n.error,\n.plain {\n  /* width: min-content; */\n  height: -webkit-min-content;\n  height: -moz-min-content;\n  height: min-content;\n  min-width: 100px;\n  padding: 0.5em 20px;\n  text-align: center;\n  white-space: pre-wrap;\n  line-height: 30px;\n  font-size: 1.1rem;\n  border-radius: 10px;\n  /* box-shadow: rgb(0 0 0 / 16%) 0px 3px 6px, rgb(0 0 0 / 23%) 0px 3px 6px; */\n  background-color: white;\n  border: 1px solid #aaa;\n}\n.error {\n  color: red;\n}\n.plain {\n  color: yellow;\n}\n.success {\n  color: green;\n}\n@media only screen and (max-width: 483px) {\n.success,\n  .error,\n  .plain {\n    width: 100%;\n    white-space: nowrap;\n    font-size: 1rem;\n}\n}\n"