export default "\n.container {\n  width: 100vw;\n  /* height: 100vh; */\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n  background: #f9f9f9;\n  --padding-left: 5vw;\n}\n.title {\n  font-size: 2.5em;\n  font-weight: bold;\n  /* font-family: youshe; */\n  white-space: nowrap;\n  color: #4d4d4d;\n  /* margin-bottom: 2vh; */\n}\n.content {\n  /* margin-bottom: 5vh; */\n}\n.content {\n  line-height: 1.5;\n}\n.title span {\n  color: #93d500;\n}\n.buttonGroup {\n  display: flex;\n  flex-wrap: nowrap;\n  width: 90vw;\n  justify-content: center;\n  /* margin-top: 5vh; */\n}\n.top {\n  /* padding-top: 10vh; */\n  display: flex;\n  padding-left: var(--padding-left);\n}\n.topBox {\n  width: -webkit-min-content;\n  width: -moz-min-content;\n  width: min-content;\n  display: flex;\n  flex-direction: column;\n  width: 90vw;\n  box-sizing: border-box;\n}\n.list {\n  width: 90vw;\n  box-sizing: border-box;\n  /* height: 45vh; */\n  display: inline-flex;\n  flex-direction: column;\n  justify-content: space-between;\n  align-items: flex-start;\n  padding-left: 5vw;\n}\ncommon-logobox {\n  border-bottom: 0.5px solid #ccc;\n  transform: scale(0.7);\n  transform-origin: left;\n}\ncommon-logobox:nth-child(4) {\n  border-bottom: none;\n}\n"