export default "\n.container {\n  position: relative;\n  width: 100px;\n  height: 100px;\n  overflow: hidden;\n  display: inline-block;\n}\n.arrowTop,\n.arrowBottom,\n.arrowMiddle {\n  position: absolute;\n  box-sizing: border-box;\n  width: 100px;\n  margin: 49px 0;\n  height: 2px;\n  border-bottom: 2px dashed rgb(216, 224, 236);\n}\n.arrowTop,\n.arrowBottom {\n  width: 30px;\n  margin-left: 70px;\n  transform-origin: right;\n}\n.arrowTop {\n  transform: rotate(30deg);\n}\n.arrowBottom {\n  transform: rotate(-30deg);\n}\n"