export default "\n.container {\n  white-space: nowrap;\n  width: 100%;\n  min-width: -webkit-min-content;\n  min-width: -moz-min-content;\n  min-width: min-content;\n  overflow-x: scroll;\n  display: flex;\n  justify-content: space-around;\n}\n.container::-webkit-scrollbar {\n  display: none;\n}\n.columnBox {\n  display: inline-flex;\n  flex-direction: column;\n  width: -webkit-min-content;\n  width: -moz-min-content;\n  width: min-content;\n  align-items: stretch;\n  flex-grow: 1;\n}\n.column {\n  white-space: nowrap;\n  padding: 0 1em;\n  height: 3em;\n  line-height: 3em;\n  text-align: left;\n  border-bottom: 1px solid #ddd;\n}\n.column:nth-last-child(1) {\n  border-bottom: none;\n}\n"