export default "\n.container {\n  width: 100vw;\n  height: 100vh;\n  min-width: 1000px;\n  display: flex;\n  flex-wrap: nowrap;\n  justify-content: space-between;\n}\n.buttonGroup {\n  padding-bottom: 3em;\n  min-width: 400px;\n  display: flex;\n  flex-wrap: nowrap;\n  justify-content: flex-start;\n}\n.shouldKnow {\n  transform: translateY(100%);\n  width: 0;\n  white-space: nowrap;\n  font-size: 0.7rem;\n  color: #4d4d4d;\n}\n.shouldKnow > a {\n  text-decoration: none;\n  color: #93d500;\n}\n.buttonGroup .space {\n  /* margin-right: 2em; */\n}\n.left,\n.right {\n  flex-grow: 1;\n  width: -webkit-min-content;\n  width: -moz-min-content;\n  width: min-content;\n  min-width: 500px;\n  display: flex;\n}\n.right {\n  padding-top: 5em;\n  box-sizing: border-box;\n  align-items: center;\n  justify-content: center;\n}\n.left {\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  box-sizing: border-box;\n}\n.leftBox {\n  padding-left: 5em;\n}\n.title {\n  font-size: 4em;\n  font-weight: bold;\n  color: #4d4d4d;\n  /* font-family: youshe; */\n  white-space: nowrap;\n  margin-top: 0;\n  margin-bottom: 7vw;\n  padding-top: 0;\n}\n.secTitle {\n  font-size: 1.5em;\n  margin-bottom: 0;\n  color: #93d500;\n}\n"