export default "\ndiv.box {\n  display: inline-block;\n  position: relative;\n  background: #93d500;\n  transition: background 0.5s ease;\n  color: white;\n  border-radius: 10px;\n  font-size: 1.2rem;\n  min-width: 160px;\n  height: 50px;\n  line-height: 50px;\n  text-align: center;\n  cursor: pointer;\n  /* border: 1px solid #b3b3b3; */\n  /* box-shadow: 1px 1px 1px 0 #ddd,2px 2px 4px 0 rgba(242, 242, 242, 0.77); */\n}\n.toast {\n  position: absolute;\n  box-sizing: border-box;\n  padding: 1em 1.5em;\n  border: 1px solid #aaa;\n  border-radius: 5px;\n  width: 300px;\n  font-size: 0.7em;\n  height: -webkit-min-content;\n  height: -moz-min-content;\n  height: min-content;\n  line-height: 1.2;\n  color: #4d4d4d;\n  transform: translate(0, -50%);\n  left: 0;\n  top: -100%;\n  background: rgba(255, 255, 255, 1);\n  text-align: left;\n}\n.toggle {\n  display: none;\n}\ndiv.box:hover {\n  background: #a4dc29;\n}\ndiv.box:hover .toggle {\n  display: block;\n}\n.off {\n  display: none !important;\n}\n"