export default "\n.container {\n  width: 100vw;\n  /* height: 100vh; */\n  display: flex;\n  flex-direction: column;\n  --padding-left: 5vw;\n  justify-content: space-between;\n  padding-left: var(--padding-left);\n  --margin-bottom: min(2vh, 2rem);\n}\n.title {\n  font-size: 2.5em;\n  font-weight: bold;\n  /* font-family: youshe; */\n  white-space: nowrap;\n  /* margin-top: 10vh; */\n  color: #4d4d4d;\n  margin-bottom: 0.2rem;\n}\n.title span {\n  color: #93d500;\n}\n.top {\n  padding-left: var(--padding-left);\n}\n.env {\n  width: 90vw;\n  /* height: 5vh; */\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n.textBox {\n  height: -webkit-min-content;\n  height: -moz-min-content;\n  height: min-content;\n  flex-grow: 1;\n  margin-bottom: 5em;\n}\ncommon-workenv {\n  flex-shrink: 1;\n  /* width: 1356px; */\n  /* height: 1415px; */\n  transform-origin: center;\n}\n.secTitle,\n.buttonTitle {\n  font-size: 1.2rem;\n  color: #4d4d4d;\n  margin-bottom: var(--margin-bottom);\n  padding-left: 2.5rem;\n  font-weight: bold;\n}\n.content {\n  line-height: 2;\n  font-size: 0.8rem;\n  padding-left: 2.5rem;\n  color: #666;\n  margin-bottom: var(--margin-bottom);\n}\n.buttonTitle {\n  margin-top: calc(2 * var(--margin-bottom));\n}\n.buttonBox {\n  width: 90vw;\n  display: flex;\n  justify-content: center;\n}\n.buttonBox > *:nth-child(1) {\n  margin-right: 1em;\n}\n"