export default "\n.required,\n.error {\n  color: red;\n}\n.hide {\n  color: transparent;\n}\n.container {\n  --i-content-size: 14px;\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n  align-items: flex-start;\n  min-width: 17em;\n  margin: 1em;\n  margin-bottom: 2em;\n}\n.error {\n  font-size: 0.9em;\n  /* font-weight: bold; */\n}\n.input {\n  outline: none;\n  font-size: var(--i-content-size) !important;\n  height: calc(var(--i-content-size) * 2.2);\n  width: 100%;\n  border-radius: 8px;\n  border: 1px solid #c8cccf;\n  outline: 0;\n  box-sizing: border-box;\n  padding-left: 20px;\n}\n.inputPaler::-moz-placeholder {\n  opacity: 0.5;\n}\n.inputPaler::placeholder {\n  opacity: 0.5;\n}\n"